    .tooltip {
        position: relative;
        display: inline-block;
    }

    .tooltip .tooltiptext {
        visibility: hidden;
        position: absolute;
        top: 125%;
        left: 50%;
        z-index: 1;
        margin-left: -60px;
        padding: 5px 0;
        width: 175px;
        color: rgb(255 255 255);
        font-size: 14px;
        text-align: center;
        background-color: rgb(0 0 0);
        border-radius: 6px;
        opacity: 0;
        transition: opacity 1s ease 0s;
    }

    .tooltip:hover .tooltiptext {
        visibility: visible;
        font-size: 14px;
        opacity: 1;
    }

    .player-opened {
        padding-bottom: 80px;
    }

