        color: rgb(255 255 255);
        background: var(--ghost-accent-color-night-eye);
    }

    .kg-header-card.kg-style-accent a.kg-header-card-button {
        color: rgb(191 191 191);
        background: rgb(28 28 33);
    }

    @media (max-width: 640px) {
        .kg-header-card {
            padding-right: 1em;
            padding-left: 1em;
        }

        .kg-header-card h2.kg-header-card-header {
            font-size: 3.5em;
        }

        .kg-header-card.kg-size-large h2.kg-header-card-header {
            font-size: 4em;
        }

        .kg-header-card.kg-size-small h2.kg-header-card-header {
            font-size: 3em;
        }

        .kg-header-card h3.kg-header-card-subheader {
            font-size: 1.25em;
        }

        .kg-header-card.kg-size-large h3.kg-header-card-subheader {
            font-size: 1.5em;
        }

        .kg-header-card.kg-size-small h3.kg-header-card-subheader {
            font-size: 1em;
        }
    }

    .kg-nft-card,
    .kg-nft-card * {
        box-sizing: border-box;
    }

    .kg-nft-card {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: auto;
        margin-left: auto;
        width: 100%;
    }

    .kg-nft-card a.kg-nft-card-container {
        position: static;
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        max-width: 512px;
        width: 100%;
        color: rgb(191 191 191);
        font-family: -apple-system, BlinkMacSystemFont, "avenir next", avenir, "helvetica neue", helvetica, ubuntu, roboto, noto, "segoe ui", arial, sans-serif;
        font-size: 14px;
        font-weight: 400;
        text-decoration: none;
        background: rgb(28 28 33);
        border-radius: 5px;
        box-shadow: rgb(0 0 0 / 10%) 0 2px 6px -2px, rgb(0 0 0 / 40%) 0 0 1px;
        transition: none 0s ease 0s;
    }

    .kg-nft-card * {
        position: static;
    }

    .kg-nft-metadata {
        padding: 20px;
        width: 100%;
    }

    .kg-nft-image {
        width: 100%;
        border-radius: 5px 5px 0 0;
    }

    .kg-nft-header {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 20px;
    }

    .kg-nft-header h4.kg-nft-title {
        margin: 0;
        min-width: unset;
        max-width: unset;
        color: rgb(191 191 191);
        font-family: inherit;
        font-size: 19px;
        line-height: 1.3em;
        font-weight: 700;
    }

    .kg-nft-opensea-logo {
        margin-top: 2px;
        width: 100px;
        object-fit: scale-down;
    }

    .kg-nft-card p.kg-nft-description,
    .kg-nft-creator {
        margin: 4px 0 0;
        color: rgb(191 191 191);
        font-family: inherit;
        line-height: 1.4em;
    }

    .kg-nft-creator span {
        color: rgb(191 191 191);
        font-weight: 500;
    }

    .kg-nft-card p.kg-nft-description {
        margin: 20px 0 0;
        color: rgb(191 191 191);
        font-size: 14px;
    }

    .kg-product-card,
    .kg-product-card * {
        box-sizing: border-box;
    }

    .kg-product-card {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .kg-product-card-container {
        display: grid;
        align-items: center;
        padding: 20px;
        max-width: 550px;
        width: 100%;
        background: 0 0;
        border-radius: 5px;
        box-shadow: rgb(125 140 155 / 25%) 0 0 0 1px inset;
        grid-template-columns: auto min-content;
        row-gap: 16px;
    }

    .kg-product-card-image {
        grid-column: 1 / 3;
        justify-self: center;
    }

    .kg-product-card-title-container {
        grid-column: 1 / 2;
    }

    .kg-product-card h4.kg-product-card-title {
        margin-top: 0;
        margin-bottom: 0;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
        font-size: 1.4em;
        line-height: 1.15em;
        font-weight: 700;
        text-decoration: none;
    }

    .kg-product-card-description {
        grid-column: 1 / 3;
    }

    .kg-product-card .kg-product-card-description ol,
    .kg-product-card .kg-product-card-description p,
    .kg-product-card .kg-product-card-description ul {
        margin-bottom: 0;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
        font-size: 0.95em;
        line-height: 1.5em;
        opacity: 0.7;
    }

    .kg-product-card .kg-product-card-description p:first-of-type {
        margin-top: -4px;
    }

    .kg-product-card .kg-product-card-description ol,
    .kg-product-card .kg-product-card-description p:not(:first-of-type),
    .kg-product-card .kg-product-card-description ul {
        margin-top: 0.95em;
    }

    .kg-product-card .kg-product-card-description li + li,
    .kg-toggle-card li + li {
        margin-top: 0.5em;
    }

    .kg-product-card-rating {
        display: flex;
        align-items: center;
        grid-column: 2 / 3;
        place-self: start end;
        padding-left: 16px;
    }

    @media (max-width: 400px) {
        .kg-product-card-title-container {
            grid-column: 1 / 3;
        }

        .kg-product-card-rating {
            grid-column: 1 / 3;
            justify-self: start;
            margin-top: -15px;
            padding-left: 0;
        }
    }

    .kg-product-card-rating-star {
        width: 20px;
        height: 28px;
    }

    .kg-product-card-rating-star svg {
        width: 16px;
        height: 16px;
        fill: currentColor;
        opacity: 0.15;
    }

    .kg-product-card a.kg-product-card-button {
        position: static;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 1.2em;
        width: 100%;
        height: 2.4em;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
        font-size: 0.95em;
        line-height: 1em;
        font-weight: 600;
        text-decoration: none;
        border-radius: 5px;
        transition: opacity 0.2s ease-in-out 0s;
        grid-column: 1 / 3;
    }

    .kg-product-card a.kg-product-card-btn-accent {
        color: rgb(255 255 255);
        background-color: var(--ghost-accent-color-night-eye);
    }

    .kg-toggle-card,
    .kg-toggle-card * {
        box-sizing: border-box;
    }

    .kg-toggle-card {
        padding: 1.2em;
        background: 0 0;
        border-radius: 4px;
        box-shadow: rgb(125 140 155 / 25%) 0 0 0 1px inset;
    }

    .kg-toggle-card[data-kg-toggle-state="close"] .kg-toggle-content {
        position: relative;
        top: -0.5em;
        overflow: hidden;
        height: 0;
        opacity: 0;
        transition: opacity 0.5s ease 0s, top 0.35s ease 0s;
    }

    .kg-toggle-content {
        position: relative;
        top: 0;
        height: auto;
        opacity: 1;
        transition: opacity 1s ease 0s, top 0.35s ease 0s;
    }

    .kg-toggle-card[data-kg-toggle-state="close"] svg {
        transform: unset;
    }

    .kg-toggle-heading {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        cursor: pointer;
    }

    .kg-toggle-card h4.kg-toggle-heading-text {
        margin-top: 0;
        margin-bottom: 0;
        font-size: 1.15em;
        line-height: 1.3em;
        font-weight: 700;
    }

    .kg-toggle-content p:first-of-type {
        margin-top: 0.5em;
    }

    .kg-toggle-card .kg-toggle-content ol,
    .kg-toggle-card .kg-toggle-content p,
    .kg-toggle-card .kg-toggle-content ul {
        margin-top: 0.95em;
        margin-bottom: 0;
        font-size: 0.95em;
        line-height: 1.5em;
    }

    .kg-toggle-card-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 1em;
        width: 24px;
        height: 24px;
        border: 0;
        background: 0 0;
    }

    .kg-toggle-heading svg {
        width: 14px;
        color: rgb(183 191 200 / 50%);
        transition: all 0.3s ease 0s;
        transform: rotate(-180deg);
    }

    .kg-toggle-heading path {
        fill: none;
        stroke: currentColor;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-width: 1.5;
        fill-rule: evenodd;
    }

    .kg-toggle-card + .kg-toggle-card {
        margin-top: 1em;
    }

    .kg-video-card,
    .kg-video-card * {
        box-sizing: border-box;
    }

    .kg-video-card {
        position: relative;

        --seek-before-width: 0%;
        --volume-before-width: 100%;
        --buffered-width: 0%;
        --seek-before-width-night-eye: 0%;
        --volume-before-width-night-eye: 100%;
        --buffered-width-night-eye: 0%;
    }

    .kg-video-card video {
        display: block;
        max-width: 100%;
        height: auto;
    }

    .kg-video-container {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .kg-video-large-play-icon,
    .kg-video-overlay {
        display: flex;
        justify-content: center;
        align-items: center;
        transition: opacity 0.2s ease-in-out 0s;
    }

    .kg-video-overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 999;
        background-image: linear-gradient(rgb(0 0 0 / 30%) 0, transparent 70%, transparent 100%);
    }

    .kg-video-large-play-icon {
        padding: 0;
        width: 72px;
        height: 72px;
        background: rgb(0 0 0 / 50%);
        border-radius: 50%;
    }

    .kg-video-large-play-icon svg {
        margin-left: 2px;
        width: 20px;
        height: auto;
        fill: rgb(255 255 255);
    }

    .kg-video-player-container {
        position: absolute;
        bottom: 0;
        z-index: 999;
        width: 100%;
        height: 80px;
        background: linear-gradient(transparent, rgb(0 0 0 / 50%));
        transition: opacity 0.2s ease-in-out 0s;
    }

    .kg-video-player {
        position: absolute;
        bottom: 0;
        z-index: 9999;
        display: flex;
        align-items: center;
        padding: 12px 16px;
        width: 100%;
    }

    .kg-video-current-time,
    .kg-video-time {
        font-family: inherit;
        font-size: 0.85em;
        line-height: 1.4em;
        font-weight: 500;
        white-space: nowrap;
    }

    .kg-video-current-time {
        padding: 0 4px;
        min-width: 38px;
        color: rgb(255 255 255);
    }

    .kg-video-time {
        color: rgb(255 255 255 / 60%);
    }

    .kg-video-duration {
        padding: 0 4px;
    }

    .kg-video-pause-icon,
    .kg-video-play-icon {
        position: relative;
        padding: 0 4px 0 0;
        font-size: 0;
        background: 0 0;
    }

    .kg-video-hide {
        display: none !important;
    }

    .kg-video-hide-animated {
        opacity: 0 !important;
        cursor: initial;
        transition: opacity 0.2s ease-in-out 0s;
    }

    .kg-video-pause-icon svg,
    .kg-video-play-icon svg {
        width: 14px;
        height: 14px;
        fill: rgb(255 255 255);
    }

    .kg-video-seek-slider {
        flex-grow: 1;
        margin: 0 4px;
    }

    @media (max-width: 520px) {
        .kg-video-seek-slider {
            display: none;
        }
    }

    .kg-video-playback-rate {
        padding: 0 4px;
        min-width: 37px;
        color: rgb(255 255 255);
        font-family: inherit;
        font-size: 0.85em;
        line-height: 1.4em;
        font-weight: 600;
        text-align: left;
        white-space: nowrap;
        background: 0 0;
    }

    @media (max-width: 520px) {
        .kg-video-playback-rate {
            padding-left: 8px;
        }
    }

    .kg-video-mute-icon,
    .kg-video-unmute-icon {
        position: relative;
        bottom: -1px;
        padding: 0 4px;
        font-size: 0;
        background: 0 0;
    }

    @media (max-width: 520px) {
        .kg-video-mute-icon,
        .kg-video-unmute-icon {
            margin-left: auto;
        }
    }

    .kg-video-mute-icon svg,
    .kg-video-unmute-icon svg {
        width: 16px;
        height: 16px;
        fill: rgb(255 255 255);
    }

    .kg-video-volume-slider {
        width: 80px;
    }

    @media (max-width: 300px) {
        .kg-video-volume-slider {
            display: none;
        }
    }

    .kg-video-seek-slider::before,
    .kg-video-volume-slider::before {
        content: "";
        position: absolute;
        left: 0;
        width: var(--seek-before-width) !important;
        height: 4px;
        background-color: rgb(43 43 43);
        border-radius: 2px;
        cursor: pointer;
    }

    .kg-video-volume-slider::before {
        width: var(--volume-before-width) !important;
    }

    .kg-video-card input[type="range"] {
        position: relative;
        padding: 0;
        height: auto;
        border: 0;
        background: 0 0;
        appearance: none;
    }

    .kg-video-card input[type="range"]::-webkit-slider-thumb {
        appearance: none;
    }

    .kg-video-card input[type="range"]::-webkit-slider-runnable-track {
        width: 100%;
        height: 4px;
        background: rgb(28 28 33 / 20%);
        border-radius: 2px;
        cursor: pointer;
    }

    .kg-video-card input[type="range"]::-webkit-slider-thumb {
        position: relative;
        box-sizing: content-box;
        margin: -5px 0 0;
        width: 13px;
        height: 13px;
        border: 0;
        background: rgb(28 28 33);
        border-radius: 50%;
        box-shadow: rgb(0 0 0 / 8%) 0 0 0 1px, rgb(0 0 0 / 24%) 0 1px 4px;
        cursor: pointer;
    }

    .kg-video-card input[type="range"]:active::-webkit-slider-thumb {
        transform: scale(1.2);
    }

    code[class*="language-"],
    pre[class*="language-"] {
        color: rgb(204 204 204);
        font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
        font-size: 1em;
        line-height: 1.5;
        text-align: left;
        white-space: pre;
        word-spacing: normal;
        word-break: normal;
        tab-size: 4;
        hyphens: none;
        background: 0 0;
        overflow-wrap: normal;
    }

    pre[class*="language-"] {
        overflow: auto;
        margin: 0.5em 0;
        padding: 1em;
    }

    :not(pre) > code[class*="language-"],
    pre[class*="language-"] {
        background: rgb(46 46 46);
    }

    :not(pre) > code[class*="language-"] {
        padding: 0.1em;
        white-space: normal;
        border-radius: 0.3em;
    }

    .token.block-comment,
    .token.cdata,
    .token.comment,
    .token.doctype,
    .token.prolog {
        color: rgb(191 191 191);
    }

    .token.punctuation {
        color: rgb(204 204 204);
    }

    .token.attr-name,
    .token.deleted,
    .token.namespace,
    .token.tag {
        color: rgb(230 153 156);
    }

    .token.function-name {
        color: rgb(159 191 224);
    }

    .token.boolean,
    .token.function,
    .token.number {
        color: rgb(230 184 153);
    }

    .token.class-name,
    .token.constant,
    .token.property,
    .token.symbol {
        color: rgb(230 205 153);
    }

    .token.atrule,
    .token.builtin,
    .token.important,
    .token.keyword,
    .token.selector {
        color: rgb(212 170 213);
    }

    .token.attr-value,
    .token.char,
    .token.regex,
    .token.string,
    .token.variable {
        color: rgb(166 216 185);
    }

    .token.entity,
    .token.operator,
    .token.url {
        color: rgb(159 223 222);
    }

    .token.bold,
    .token.important {
        font-weight: 700;
    }

    .token.italic {
        font-style: italic;
    }

    .token.entity {
        cursor: help;
    }

    .token.inserted {
        color: rgb(153 230 153);
    }

    :root {
        --ghost-accent-color: hsl(340deg 60% 75% / 100%);
        --ghost-accent-color-night-eye: hsl(340deg 60% 59% / 100%);
    }

    .App {
        text-align: center;
    }

    .App-logo {
        height: 40vmin;
        pointer-events: none;
    }

    @media (prefers-reduced-motion: no-preference) {
        .App-logo {
            animation: 20s linear 0s infinite normal none running App-logo-spin;
        }
    }

    .App-header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        color: rgb(255 255 255);
        font-size: calc(10px + 2vmin);
        background-color: rgb(40 44 52);
    }

    .App-link {
        color: rgb(153 213 230);
    }

    @keyframes App-logo-spin {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }

    @keyframes App-logo-spin {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }

    .fluid-width-video-wrapper {
        position: relative;
        padding: 0;
        width: 100%;
    }

    .fluid-width-video-wrapper iframe,
    .fluid-width-video-wrapper object,
    .fluid-width-video-wrapper embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}
